.menuWrapper {
    width: 328px;
    right: 10px;
    top: 80px;
    background-color: var(--color-fill-common-secondary);
    box-shadow: 0px 15px 12px rgba(0, 0, 0, 0.1), 0px 19px 38px rgba(0, 0, 0, 0.1);
}
.menuWrapper .menuListItem .MuiListItemText-root span {
    color: #121212 !important;
    text-align: justify;
    white-space: normal;
    overflow: visible;
}
.listHeader{
    height: 60px;
    /* border-bottom: 1px solid #ccc; */
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    background-color: var(--color-fill-common-tertiary);
    border-bottom: var(--effect-border-width-regular) solid var(--color-border-common-primary);
}
.menuWrapper > div > span:first-child{
    margin: 12px 15px;
    font-size: 18px;
    font-weight: bold;
    float: left;
}
.menuWrapper > div > span:last-child{
    float:left;
    margin:20px;
    font-family: 'Poppins',sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: -0.25px;
    color: var(--color-text-common-primary);
}
.menuWrapper > div > span:last-child > span > span{
    display: inline-block;
    vertical-align: middle;
    margin-left:3px;
}
.menuWrapper > ul {
    background: var(--color-fill-common-secondary);
    padding: 2px;
    display: flex;
    flex-direction: column;
}
.menuListItem {
    background: #EFFCF6;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
    width: auto !important;
    border-radius: 5px;
    margin: 4px;
    padding:2px 10px !important;
}
.userInitials{
    font-size: 12px !important;
    font-family: 'Poppins',sans-serif;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
    text-align: center;
    color: #FFFFFF;
    width: 32px !important;
    height: 32px !important;
}
.notificationType{
    color: #C4C4C4;
    bottom: 7px;
    position: relative;
    padding-left: 10px;
}
.listInitials{
    padding:0 0 0 15px;
    width: 32px;
    height: 32px;
    position: relative;
    bottom: 20px;
}

/* view all */
.viewAll{
    background-color: var(--color-fill-common-tertiary);
    border-top: var(--effect-border-width-regular) solid var(--color-border-common-primary);
    padding: var(--spacing-padding-medium);
}
.viewAll > button {
    width: 100%;
    background: var(--color-fill-cta-primary-default) !important;
    color: var(--color-text-common-inverse-primary);
    text-transform: capitalize;
    font-family: 'Poppins',sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    padding: 10px;
}
.viewAll .MuiButtonBase-root:hover{
    background-color: var(--color-fill-cta-primary-hover) !important;
    border-color: var(--color-border-cta-primary-hover) !important;
}
.errorInfo{
    text-align: center;
    padding: 10px;
    line-height: 0.5em;
    color: var(--color-text-common-secondary);
    background-color: var(--color-fill-common-secondary);
    font-family: 'Noto Sans',sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
}
.errorInfo > p{
    font-family: 'Noto Sans',sans-serif;
    font-style: normal;
}
.isRead{
    background:#fff;
    cursor: auto;
}